<template>
  <div id="addRooms_box">
    <van-form
      @submit="onSubmit"
      id="scroll-pro"
    >
      <!-- 房间号 -->
      <van-field
        v-if="!dataForm.id || dataForm.id && readonly == false"
        v-model="dataForm.roomSn"
        maxlength="20"
        :readonly="dataForm.syn == '1' && dataForm.id && dataForm.mac && ROLE_MANAGER == false?true:false"
        label="房间号"
        placeholder="请输入房间号"
      />
      <div
        v-else
        class="scale_box between"
      >
        <div class="w6">房间号</div>
        <div class="flex c_00 ">{{ dataForm.roomSn }}</div>
      </div>
      <!-- 基础房型 -->
      <div
        class="scale_box"
        :class="dataForm.id && readonly == true?'between':''"
      >
        <div class="w6">基础房型</div>
        <div
          class="flex c_00"
          @click="!dataForm.id || dataForm.id && readonly == false?show_scale = true:''"
        >
          <div>{{ scaleName }}</div>
          <van-icon
            v-if="!dataForm.id || dataForm.id && readonly == false"
            name="arrow"
          />
        </div>
      </div>
      <!-- 房间地址 -->
      <van-field
        v-if="!dataForm.id || dataForm.id && readonly == false"
        style="margin-top:10px;"
        v-model="dataForm.buildNum"
        :readonly="dataForm.syn == '0' && ROLE_MANAGER == true || !dataForm.id?false :true"
        maxlength="5"
        label="楼栋号"
        placeholder="请输入楼栋号"
      />
      <van-field
        v-if="!dataForm.id || dataForm.id && readonly == false"
        v-model="dataForm.unit"
        :readonly="dataForm.syn == '0' && ROLE_MANAGER == true || !dataForm.id?false :true"
        maxlength="5"
        label="单元号"
        placeholder="请输入单元号"
      />
      <van-field
        v-if="!dataForm.id || dataForm.id && readonly == false"
        v-model="dataForm.floor"
        type="number"
        :readonly="dataForm.syn == '0' && ROLE_MANAGER == true || !dataForm.id?false :true"
        label="楼层"
        placeholder="请输入楼层"
      />
      <div
        v-else
        style="margin-top:10px;"
        class="scale_box"
        :class="dataForm.id?'between':''"
      >
        <div class="w6">房间地址</div>
        <div class="flex c_00">
          <div>{{ dataForm.buildNum }}栋{{ dataForm.unit }}单元{{ dataForm.floor }}层</div>
        </div>
      </div>
      <!-- 房间等级 -->
      <div
        class="scale_box"
        :style="{'margin-top':dataForm.id?'10px':''}"
        :class="dataForm.id && readonly == true?'between':''"
        @click="!dataForm.id || dataForm.id && readonly == false?show_level = true:''"
      >
        <div class="w6">房间等级</div>
        <div class="flex c_00">
          <div>{{ roomLevel }}</div>
          <van-icon
            v-if="!dataForm.id || dataForm.id && readonly == false"
            name="arrow"
          />
        </div>
      </div>
      <!-- 分房排序 -->
      <van-field
        v-if="!dataForm.id || dataForm.id && readonly == false"
        v-model="dataForm.roomSort"
        type="number"
        :rules="roomSortRules"
        :readonly='readonly'
        label="分房排序"
        placeholder="请输入数字，数字越小越优先分房"
      />
      <div
        v-else
        class="scale_box"
        :class="dataForm.id && readonly == true?'between':''"
      >
        <div class="w6">分房排序</div>
        <div class="flex c_00">
          <div>{{ dataForm.roomSort }}</div>
        </div>
      </div>
      <!-- 分房排序 -->
      <!-- && c_hotelType == '4' -->
      <div v-if="c_hotelType == '4'">
        <van-field
          v-if="!dataForm.id || dataForm.id && readonly == false"
          v-model="dataForm.bedNum"
          type="number"
          :rules="roomSortRules"
          :readonly='dataForm.id && readonly == false'
          label="床位"
          placeholder="请输入床位数量"
        />
        <div
          v-else
          class="scale_box"
          :class="dataForm.id && readonly == true?'between':''"
        >
          <div class="w6">床位</div>
          <div class="flex c_00">
            <div>{{ dataForm.bedNum }}</div>
          </div>
        </div>
      </div>
      <!-- 门锁类型 -->
      <div
        v-if="!dataForm.id || dataForm.id && !dataForm.mac && readonly == false"
        style="margin-top:10px;"
        class="scale_box"
        :class="dataForm.id && readonly == true?'between':''"
      >
        <div class="w6">门锁类型</div>
        <div
          class="flex c_00"
          @click="!dataForm.id || dataForm.id && !dataForm.mac && readonly == false?show_lock = true:''"
        >
          <div>{{ hotelLockType }}</div>
          <van-icon
            v-if="!dataForm.id || dataForm.id && !dataForm.mac && readonly == false"
            name="arrow"
          />
        </div>
      </div>
      <div
        v-else-if="dataForm.id && !dataForm.mac && readonly == true"
        style="margin-top:10px;"
        class="scale_box between"
      >
        <div class="w6">门锁类型</div>
        <div class="flex c_00">
          <div>{{ hotelLockType }}</div>
        </div>
      </div>
      <!-- 装锁方式 -->
      <div
        v-if="!dataForm.id || dataForm.id && !dataForm.mac && readonly == false"
        class="scale_box"
      >
        <div class="w6">装锁方式</div>
        <van-radio-group
          class="c_00"
          v-model="lockInstall"
          v-for="(item,i) in lockInstallOldAll"
          :key="i"
        >
          <van-radio
            checked-color="#ff7d40"
            :name="item.dictLabel"
          >{{ item.dictLabel }}</van-radio>
        </van-radio-group>
      </div>
      <div
        v-else-if="dataForm.id && !dataForm.mac && readonly == true"
        class="scale_box between"
      >
        <div class="w6">装锁方式</div>
        <div class="flex c_00">
          <div>{{ lockInstall }}</div>
        </div>
      </div>
      <!-- 开门方式 -->
      <div
        v-if="!dataForm.id || dataForm.id && !dataForm.mac && readonly == false"
        class="scale_box"
      >
        <div class="w6">开门方式</div>
        <div class="door_box">
          <div
            v-for="(item,i) in doorTypeOldAll"
            :key="i"
            :class="doorType == item.dictLabel?'choosed':''"
            @click="choosedDoor(item)"
            class="door_content"
          >
            <div>{{ item.dictLabel }}</div>
          </div>
        </div>
      </div>
      <div
        v-else-if="dataForm.id && !dataForm.mac && readonly == true"
        class="scale_box between"
      >
        <div class="w6">开门方式</div>
        <div class="flex c_00">
          <div>{{ doorType }}</div>
        </div>
      </div>
      <!-- Mac地址 -->
      <div
        v-if="dataForm.id && dataForm.mac && readonly == true"
        class="scale_box between"
        style="margin-top:10px;"
      >
        <div class="w6">Mac地址</div>
        <div class="flex c_00">
          <div>{{ dataForm.mac }}</div>
        </div>
      </div>
      <!-- 房间二维码 -->
      <div
        v-if="dataForm.id && dataForm.lockSn && readonly == true"
        class="scale_box between"
      >
        <div class="w6">房间二维码</div>
        <div class="flex c_00">
          <div>{{ dataForm.lockSn }}</div>
        </div>
      </div>
      <!-- 所属业主 -->
      <div
        v-if="c_hotelType == '5'"
        class="scale_box"
        :class="dataForm.id && readonly == true?'between':''"
      >
        <div class="w6">所属业主</div>
        <div
          class="flex c_00"
          @click="!dataForm.id || dataForm.id && readonly == false?show_person = true:''"
        >
          <div>{{ landlord || '无' }}</div>
          <van-icon
            v-if="!dataForm.id || dataForm.id && readonly == false"
            name="arrow"
          />
        </div>
      </div>
      <!-- 管理提成 -->
      <div v-if="landlord != '无' && landlord">
        <van-field
          v-if="!dataForm.id && c_hotelType == '5' || dataForm.id && readonly == false && c_hotelType == '5'"
          v-model="dataForm.maCommission"
          type="number"
          :rules="roomMaCommision"
          :readonly='readonly'
          label="管理提成"
          placeholder="请输入管理提成(0-100)"
        />
        <div
          v-else
          class="scale_box"
          :class="dataForm.id && readonly == true?'between':''"
        >
          <div class="w6">管理提成</div>
          <div class="flex c_00">
            <div>{{ dataForm.maCommission }}</div>
          </div>
        </div>
      </div>
    </van-form>
    <van-button
      v-if="!dataForm.id"
      class="add_box"
      block
      native-type="submit"
      @click="onSubmit"
    >确定新增</van-button>
    <van-button
      v-if="dataForm.id"
      class="add_box"
      block
      native-type="submit"
      @click="onSubmit"
    >{{ buttonContent }}</van-button>
    <van-button
      v-if="dataForm.id && !dataForm.mac"
      class="add_box remove_box"
      block
      native-type="submit"
      @click="removeRoom"
    >删除房间</van-button>
    <!-- 选择基础房型 -->
    <van-popup
      v-model="show_scale"
      position="bottom"
    >
      <van-picker
        show-toolbar
        :columns="scaleAll"
        @confirm="onConfirmScale"
        @cancel="show_scale = false"
      />
    </van-popup>
    <!-- 选择房间等级 -->
    <van-popup
      v-model="show_level"
      position="bottom"
    >
      <van-picker
        show-toolbar
        :columns="levelAll"
        @confirm="onConfirmLevel"
        @cancel="show_level = false"
      />
    </van-popup>
    <!-- 选择门锁类型 -->
    <van-popup
      v-model="show_lock"
      position="bottom"
    >
      <van-picker
        show-toolbar
        :columns="hotelLockTypeAll"
        @confirm="onConfirmLock"
        @cancel="show_lock = false"
      />
    </van-popup>
    <!-- 选择所属业主 -->
    <van-popup
      v-model="show_person"
      position="bottom"
    >
      <van-picker
        show-toolbar
        :columns="personAll"
        @confirm="onConfirmPenson"
        @cancel="show_person = false"
      />
    </van-popup>
  </div>
</template>
<script>
import { Toast, Dialog } from 'vant';
import { GetSystemDictDataByType } from '@/api/api_query';
import { PostBusinessRoomAdd, PostBusinessRoomEdit, PostBusinessRoomRemove, GetBusinessRoomscaleSimpleByhotel } from '@/api/api_hotel.js';
import { GetBusinessRoomRoomId, GetBusinessHotelOpRelationRoleByhotel } from '@/api/api_business_hotel_and_scale';
export default {
    data() {
        return {
            readonly: true,
            show_scale: false,
            show_level: false,
            show_lock: false,
            show_person: false,
            isRepeat: false,
            ROLE_MANAGER: false,
            scaleAll: [],
            levelAll: [],
            hotelLockTypeAll: [],
            lockInstallOldAll: [],
            doorTypeOldAll: [],
            personAll: [],
            hotelLockType: '0',
            lockInstall: null,
            roomLevel: null,
            scaleName: null,
            landlord: null,
            doorType: null,
            c_hotelType: null,
            buttonContent: '修改信息',
            dataForm: {
                hotelId: this.$route.query.hotelId,
                id: null,
                roomSn: null,
                roomScaleId: null,
                buildNum: null,
                unit: null,
                floor: null,
                hotelLockType: null,
                roomLevel: null,
                lockInstall: null,
                doorType: null,
                roomSort: null,
                bedNum: null,
                landlord: null,
                maCommission: null
            },
            roomSortRules: [
                {
                    validator: (value) => {
                        return value < 10000;
                    },
                    message: '分房排序不能大于10000',
                    trigger: 'onBlur'
                }
            ],
            roomMaCommision: [
                {
                    validator: (value) => {
                        return 0 < value && value < 100;
                    },
                    message: '管理提成须在0-100之间',
                    trigger: 'onBlur'
                }
            ]
        };
    },
    created() {
        this.$store.commit('setHeader', '房间信息');
        let login_ok_info = JSON.parse(localStorage.getItem('current_user'));
        if (login_ok_info.id == '1') {
            this.ROLE_MANAGER = true;
        } else {
            this.roles = JSON.parse(localStorage.getItem('roles'));
            this.roles.some((item) => {
                if (item.name == 'ROLE_MANAGER') {
                    return (this.ROLE_MANAGER = true);
                } else {
                    return (this.ROLE_MANAGER = false);
                }
            });
        }
        this.c_hotelType = JSON.parse(localStorage.getItem('c_hotelType'));
        if (!this.$route.query.id) {
            this.readonly = false;
            this.getType();
        } else {
            this.getCurrentInfo();
        }
    },
    methods: {
        getCurrentInfo() {
            GetBusinessRoomRoomId(this.$route.query.id).done((res) => {
                this.dataForm = { ...this.dataForm, ...res.data };
                this.getType();
            });
        },
        // 字典查询
        getType() {
            GetSystemDictDataByType({ dictType: 'business_room_level' }).done((res) => {
                this.levelOldAll = res.data;
                this.levelAll = res.data.map((item) => item.dictLabel);
                if (this.dataForm.id) {
                    if (this.dataForm.roomLevel) {
                        this.roomLevel = this.levelOldAll.find((item) => item.dictValue == this.dataForm.roomLevel).dictLabel;
                    } else {
                        this.roomLevel = '';
                    }
                } else {
                    this.roomLevel = this.levelOldAll[0].dictLabel;
                    this.dataForm.roomLevel = this.levelOldAll[0].dictValue;
                }
            });
            GetSystemDictDataByType({ dictType: 'business_lock_install_type' }).done((res) => {
                this.hotelLockTypeOldAll = res.data;
                this.hotelLockTypeAll = res.data.map((item) => item.dictLabel);
                if (this.dataForm.id) {
                    if (this.dataForm.hotelLockType) {
                        this.hotelLockType = this.hotelLockTypeOldAll.find((item) => item.dictValue == this.dataForm.hotelLockType).dictLabel;
                    } else {
                        this.hotelLockType = '';
                    }
                } else {
                    this.hotelLockType = this.hotelLockTypeOldAll[0].dictLabel;
                    this.dataForm.hotelLockType = this.hotelLockTypeOldAll[0].dictValue;
                }
            });
            GetSystemDictDataByType({ dictType: 'business_room_door_install' }).done((res) => {
                this.lockInstallOldAll = res.data;
                this.lockInstallAll = res.data.map((item) => item.dictLabel);
                if (this.dataForm.id) {
                    if (this.dataForm.lockInstall) {
                        this.lockInstall = this.lockInstallOldAll.find((item) => item.dictValue == this.dataForm.lockInstall).dictLabel;
                    } else {
                        this.lockInstall = '';
                    }
                } else {
                    this.lockInstall = this.lockInstallOldAll[0].dictLabel;
                    this.dataForm.lockInstall = this.lockInstallOldAll[0].dictValue;
                }
            });
            GetSystemDictDataByType({ dictType: 'business_room_door_type' }).done((res) => {
                this.doorTypeOldAll = res.data;
                if (this.dataForm.id) {
                    if (this.dataForm.doorType) {
                        this.doorType = this.doorTypeOldAll.find((item) => item.dictValue == this.dataForm.doorType).dictLabel;
                    } else {
                        this.doorType = '';
                    }
                } else {
                    this.dataForm.doorType = this.doorTypeOldAll[0].dictValue;
                    this.doorType = this.doorTypeOldAll[0].dictLabel;
                }
            });
            this.HotelStateListGet();
        },
        // 获取基础房型信息
        HotelStateListGet() {
            GetBusinessRoomscaleSimpleByhotel({ hotelId: this.$route.query.hotelId })
                .then((result) => {
                    let obj = {
                        scaleName: '无',
                        roomScaleId: '无',
                        id: '0'
                    };
                    result.data.unshift(obj);
                    this.scaleOldAll = result.data;
                    this.scaleAll = result.data.map((item) => item.scaleName);
                    if (!this.dataForm.id) {
                        this.dataForm.roomScaleId = this.scaleOldAll[0].id;
                        this.scaleName = this.scaleOldAll[0].scaleName;
                    } else {
                        this.scaleName = this.scaleOldAll.find((item) => item.id == this.dataForm.roomScaleId).scaleName;
                    }
                })
                .catch((e) => {
                    console.log(e);
                });
            if (this.c_hotelType == '5') {
                this.getPersonList();
            }
        },
        // 根据角色获取酒店员工列表
        getPersonList() {
            GetBusinessHotelOpRelationRoleByhotel(this.$route.query.hotelId, { role: 'ROLE_HOTEL_OWNER' }).done((res) => {
                let obj = {
                    userId: '0',
                    sysUser: {
                        realName: '无'
                    }
                };
                res.data.unshift(obj);
                this.personAll = res.data.map((item) => item.sysUser.realName);
                this.personOldAll = res.data;
                if (!this.$route.query.id) {
                    this.dataForm.landlord = this.personOldAll[0].userId;
                    this.landlord = this.personOldAll[0].sysUser.realName;
                } else {
                    if (this.dataForm.landlord) {
                        this.landlord = this.personOldAll.find((item) => item.userId == this.dataForm.landlord).sysUser.realName;
                    }
                }
            });
        },
        // 选择房间等级
        onConfirmLevel(e) {
            this.roomLevel = e;
            this.dataForm.roomLevel = this.levelOldAll.find((item) => item.dictLabel == e).dictValue;
            this.show_level = false;
        },
        // 选择基础房型
        onConfirmScale(e) {
            this.scaleName = e;
            this.dataForm.roomScaleId = this.scaleOldAll.find((item) => item.scaleName == e).id;
            this.show_scale = false;
        },
        // 选择门锁类型
        onConfirmLock(e) {
            this.hotelLockType = e;
            this.dataForm.hotelLockType = this.hotelLockTypeOldAll.find((item) => item.dictLabel == e).dictValue;
            this.show_lock = false;
        },
        // 选择开门方式
        choosedDoor(item) {
            this.dataForm.doorType = item.dictValue;
            this.doorType = item.dictLabel;
        },
        // 选择所属业主
        onConfirmPenson(e) {
            this.landlord = e;
            this.dataForm.landlord = this.personOldAll.find((item) => item.sysUser.realName == e).userId;
            this.show_person = false;
        },
        // 删除房间
        removeRoom() {
            Dialog.confirm({
                title: '确定删除？',
                message: `房间号：${this.dataForm.roomSn}`
            })
                .then(() => {
                    PostBusinessRoomRemove({ roomId: this.dataForm.id }).done((res) => {
                        Toast(res.msg);
                        setTimeout('window.history.go(-1)', 900);
                    });
                })
                .catch(() => {});
        },
        onSubmit() {
            if (this.isRepeat == true) {
                return false;
            }
            if (this.readonly == true) {
                this.readonly = false;
                this.$store.commit('setHeader', '修改信息');
                this.buttonContent = '保存修改';
                return;
            }
            if (!this.dataForm.roomSn) {
                Toast('请输入房间编号');
                return;
            }
            if (!this.dataForm.mac) {
                this.dataForm.lockInstall = this.lockInstallOldAll.find((item) => item.dictLabel == this.lockInstall).dictValue;
            }
            if (this.dataForm.roomScaleId == '无') {
                delete this.dataForm.roomScaleId;
            }
            if (this.dataForm.id) {
                this.dataForm_edit = {
                    hotelId: this.dataForm.hotelId,
                    id: this.dataForm.id,
                    roomSn: this.dataForm.roomSn,
                    roomScaleId: this.dataForm.roomScaleId,
                    buildNum: this.dataForm.buildNum,
                    unit: this.dataForm.unit,
                    floor: this.dataForm.floor,
                    hotelLockType: this.dataForm.hotelLockType,
                    roomLevel: this.dataForm.roomLevel,
                    lockInstall: this.dataForm.lockInstall,
                    doorType: this.dataForm.doorType,
                    roomSort: this.dataForm.roomSort,
                    landlord: this.dataForm.landlord,
                    bedNum: this.dataForm.bedNum,
                    maCommission: this.dataForm.maCommission
                };
                PostBusinessRoomEdit(this.dataForm_edit).done((res) => {
                    Toast(res.msg);
                    setTimeout('window.history.go(-1)', 900);
                });
            } else {
                delete this.dataForm.id;
                this.dataForm.isGate = 0;
                this.dataForm.roomName = this.dataForm.roomSn;
                if (this.landlord == '无') {
                    delete this.dataForm.landlord;
                    delete this.dataForm.maCommission;
                }
                PostBusinessRoomAdd(this.dataForm).done((res) => {
                    this.isRepeat = true;
                    Toast(res.msg);
                    setTimeout('window.history.go(-1)', 900);
                });
            }
        }
    }
};
</script>
<style scoped>
#addRooms_box {
    font-size: 0.8rem;
    margin-top: 10px;
    position: fixed;
    top: 50px;
    left: 0;
    bottom: 5px;
    right: 0;
    overflow: auto;
}
.scale_box {
    display: flex;
    padding: 10px 16px;
    background: #fff;
}
.w6 {
    width: 6.2em;
    margin-right: 12px;
    text-align: left;
    font-size: 0.8rem;
    color: #343434;
}
.flex {
    display: flex;
    align-items: center;
}
.door_box {
    display: flex;
    flex-wrap: wrap;
    justify-content: left;
}
.door_content {
    flex: 0 0 34%;
    text-align: center;
    background: #fff;
    padding: 4px 0px;
    color: #000;
    border: 1px solid #dbdbdb;
    border-radius: 5px;
    margin: 0 10px 10px 0;
}
.choosed {
    background: #ff7d40;
    color: #fff;
}
.add_box {
    width: 90%;
    margin-top: 20px;
    background: #ff7d40;
    color: #fff;
    margin: 20px auto;
    border-radius: 5px;
}
.remove_box {
    background: #fff;
    color: #fb5258;
    border: 1px solid #fb5258;
}
.c_00 {
    color: #000;
}
.van-field__control {
    color: red;
}
.between {
    justify-content: space-between;
}
/deep/ .van-field__control {
    color: #000;
}
/deep/ .van-field__label {
    font-size: 0.8rem;
    color: #343434;
}
/deep/ .van-radio {
    margin-right: 20px;
}
</style>



